<template>
  <div class="page-index" :class="{'top_hidden':topHidden}">
    <div class="trees"></div>
    <div class="banner" v-if="Number(info.showbanner)">
      <img class="banner__img" :src="host + info.banner" alt="banner" v-if="info.banner !== ''">
    </div>
    <div class="content" ref="content">
      <div class="info">
        <div v-html="info.content"></div>
      </div>
    </div>
    <div class="footer">
      <img class="footer__img" src="../../../static/images/more.png" alt="更多" @click="showNav">
    </div>
    <div class="dialog" v-show="dialog.state">
      <div class="dialog__wrap">
        <div class="dialog__content">
          <div class="dialog__head">
            <img class="dialog__img" src="../../../static/images/dialog-tree.png" alt="图标">
          </div>
          <div class="dialog__list">
            <a class="dialog__item" v-for="link in dialog.links" :href="link.type == 1?link.content:'/page/'+link.id">
              <img class="dialog__icon" :src="host + link.pic" alt="icon">
              <p class="dialog__name">{{link.iconname}}</p>
            </a>
            <a class="dialog__item" :href="host + 'feedback.html'" v-if="feedbackState">
              <img class="dialog__icon" src="../../../static/images/feedback.png" alt="icon">
              <p class="dialog__name">意见反馈</p>
            </a>
          </div>
          <a class="dialog__btn" :href="treeUrl">返回苗木信息</a>
        </div>
        <div class="dialog__close">
          <img src="../../../static/images/close.png" alt="close" @click="closeDialog">
        </div>
      </div>
    </div>
    <toast-box :toast="toast"></toast-box>
  </div>
</template>
<script type="text/ecmascript-6">
  import axios from 'axios'
  import toastBox from '../../components/common/toast'
  import {api} from '../../assets/js/global'

  export default {
    components: {
      toastBox
    },
    data () {
      return {
        host: 'http://tree.91zsc.com/',
        topHidden: false,
        toast: {type: 0, state: 0, text: '数据加载中'},  //  是否显示加载更多
        dialog: {state: 0, links: {}}, //  导航
        goods: {},
        api: api,
        info: {},
        treeUrl: '', // 苗木地址
        feedbackState: 0
//        cdn: picCDN
      }
    },
    methods: {
      getId () {
        let pathname = window.location.pathname
        let id = pathname.replace(/[^0-9]/ig, '')
        return id
      },
      showNav () {
        this.dialog.state = 1
      },
      closeDialog () {
        this.dialog.state = 0
      }
    },
    created () {
      this.treeUrl = window.localStorage.getItem('treeUrl')
      let vm = this
      let id = this.getId()
      this.toast.state = 1
      axios.get(this.host + 'childPage/' + id)
//      axios.get(api + 'index/id/38')
        .then(function (res) {
          vm.toast.state = 0
          vm.info = res.data.data.info
          vm.feedbackState = Number(res.data.data.feedState)
          vm.dialog.links = res.data.data.links
        })
        .catch(function (error) {
          console.log(error)
          vm.toast = {
            type: 2,
            state: 1,
            text: '加载失败，请重试'
          }
        })
    },
    mounted () {
      let vm = this
      let target = this.$refs.content
      target.addEventListener('scroll', function () {
        if (Number(vm.info.showbanner) && target.scrollTop > 10) {
          vm.topHidden = true
        }
      }, false)
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss">
  @import '../../scss/fn.scss';

  html, body, .page-index {
    height: 100%;
    overflow: hidden;
  }

  body {
    background: url("../../../static/images/bg.png") top center no-repeat;
    background-size: cover;
  }

  .page-index {
    position: relative;
    padding-top: tr(152);
    display: flex;
    flex-direction: column;
  }
  .top_hidden{
    animation: up .5s ease-out;
    padding-top: 0;
  }
  @keyframes up
  {
    from {padding-top:tr(152);}
    to {padding-top:0;}
  }
  .trees {
    position: absolute;
    width: tr(375);
    height: tr(182);
    top: 0;
    background: url("../../../static/images/trees.png") top center no-repeat;
    background-size: 100% 100%;
    z-index: 100;
  }

  .banner {
    padding-bottom: 10px;
    &__img {
      width: 100%;
    }
  }

  .title {
    padding: tr(13) tr(23) tr(10);
    &__img {
      width: tr(160);
      height: tr(31);
    }
  }

  .content {
    flex: 1;
    overflow-y: auto;
    padding: 0 tr(23) tr(10);
    -webkit-overflow-scrolling: touch;
  }

  .info {
    padding-top: 10px;
      img{
        max-width:100%;
      }
  }
  .banner__img,.info__img{
    background:url("../../assets/img/loading.gif") no-repeat center center;
    background-size: 16px 16px;
  }
  .footer {
    height: tr(65);
    padding-top: tr(6);
    text-align: center;
    &__img {
      height: tr(38);
      width: tr(186);
    }
  }
  .dialog {
    position: fixed;
    z-index: 100;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .7);
    display: flex;
    justify-content: center;
    align-items: center;
    &__wrap {
      position: relative;
    }
    &__content {
      position: relative;
      width: tr(225);
      min-height: tr(206);
      padding-top: tr(85);
      display: flex;
      flex-direction: column;
      background-color: #fff;
      z-index: 110;
      border: 1px solid rgb(187, 187, 187);
      border-radius: 5px;
      box-shadow: 0 0 15px 0 rgba(13, 4, 8, .33);
    }
    &__head {
      position: absolute;
      z-index: 200;
      top: - tr(70);
      left: 50%;
      margin-left: - tr(45);
    }
    &__img {
      width: tr(90);
      height: tr(140);
    }
    &__list {
      flex: 1;
      font-size: 0;
      display: flex;
      flex-wrap: wrap;
      padding: 0 tr(10) tr(22);
    }
    &__item {
      width: 33.3333%;
      display: inline-block;
      text-align: center;
      color: #407007;
      margin-bottom: tr(13);
    }
    &__icon {
      width: tr(43);
      height: tr(43);
    }
    &__name {
      font-size: 10px;
      font-weight: 600;
      margin-top: tr(5);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    &__btn {
      display: inline-block;
      width: 100%;
      color: #434343;
      text-align: center;
      font-size: 12px;
      line-height: 28px;
      border-top: 1px solid rgb(187, 187, 187);
      border-radius: 5px;
      box-shadow: 0 0 15px 0 rgba(13, 4, 8, 0.33);
    }
    &__close {
      position: absolute;
      right: - tr(32);
      bottom: tr(32);
      img {
        width: tr(32);
        height: tr(32);
      }
    }
  }
</style>



